import React,{useEffect, useState,} from 'react'
import {Helmet} from 'react-helmet';
import Editsection from './Editsection';
import {
    Routes,
    Route,
    useNavigate,
  } from "react-router-dom";
  import { io } from "socket.io-client";
  const URL = "harut-app.herokuapp.com"  //"localhost:5000"
  const socket = io(URL);
function Profile({dispatch,profileDisplay, ...props}) {
    const navigate = useNavigate();
    const [sendMessage, setSendMessage] = useState("");
    const [Messages, setMessages] = useState([]);
    const [activeUsers, setActiveUsers] = useState([]);
    const [chooseUser, setChooseUser] = useState("");
    const [fileType, setFileType] = useState("");
    const [sendPic, setSendPic] = useState("")
    const [user, setUser] = useState({});
    console.log(Messages)
    useEffect(() => {
        async function fetchMyAPI() {
            const response = await fetch('/api/profile', { method: 'GET', mode: 'cors',headers: {
                'Content-Type': 'application/json'
            },})
            const data = await response.json();
            data.msg !== "/"? setUser(data): navigate("/")
            
        }
        fetchMyAPI()
    },[profileDisplay])
    useEffect(() => {
       socket.emit("addUser",user.id, user.firstName, user.photo)
    },[user.firstName, user.photo])
    useEffect(() => {
        
        socket.on("hello", (data) => {
            console.log("asdasd1", data)
            if(data !== "connected") {
                window.scrollTo(0,document.body.scrollHeight);
                setMessages(oldArray =>[...oldArray,data])
            }
        });
    },[])
    useEffect(() => {
        
        socket.on("getUsers", (data) => {
            console.log(data)
            //if(JSON.stringify(data) !== JSON.stringify(activeUsers)) {
                 setActiveUsers(data)
            //}
            
        });
    },[])
    useEffect(() => {
        
        socket.on("getNotification", (data) => {
            console.log("notification", data)
            const snd = new Audio("mixkit-software-interface-start-2574.wav"); // buffers automatically when created
            snd.play();
            if(document.querySelector(`[data-id="${data.userId}"]`).style.backgroundColor !== "green") {
                document.querySelector(`[data-id="${data.userId}"]`).children[0].children[2].style.display = "inline";
            }
            
        });
    },[])

    function edit() {
        dispatch({type: "none"})
        navigate(`/profile/edit`);
    }
    function logOut() {
        socket.emit("logout")
        fetch('/logout',{method:"POST",headers: {
            'Content-Type': 'application/json'
          },
        }).then(r => navigate("/"))
    }
    async function  send() {
        let type = "";
        console.log(sendPic)
        if(sendPic !== "") {
            type="image" 
            if (fileType.indexOf("zip") !== -1) {
                type="zip"
            }
            
            if (fileType.indexOf("video") !== -1) {
                type="video"
            }
            const formData  = new FormData();
            formData.append("picture", sendPic);
            formData.append("name", user.firstName);
            formData.append("receiverId",chooseUser )
            formData.append("type", type )
           return  fetch('/api/sendmessagefile',{method:"POST",headers: {
              },
              body: formData
            }).then(r => r.json()).then(data => {
            
            console.log("mydata", data)
            
            console.log("Adsq234cfdsr")
            
            setSendPic("");
            setFileType("");
             socket.emit("hello", data.msg, chooseUser, user.firstName, user.id, type);
             window.scrollTo(0,document.body.scrollHeight);

            
            
            }).catch(e => console.log("error upload "))
        }
        const response = await fetch('/api/sendmessage',{method:"POST",headers: {
            'Content-Type': 'application/json'
          },
          body: JSON.stringify({name: user.firstName, receiverId: chooseUser, data: sendMessage})
        })
       console.log(response)
        socket.emit("hello", sendMessage, chooseUser, user.firstName, user.id, type);
        window.scrollTo(0,document.body.scrollHeight);
        document.querySelector("textarea").value = "";
        setSendMessage("");
        
    }
     function userChoose(e) {
        
        [...document.getElementById("onlineUsers").children[2].children].map(item => item.style.backgroundColor = "inherit")
        console.log(e.target, e.currentTarget)
        e.currentTarget.style.backgroundColor = "green";
        e.currentTarget.children[0].children[2].style.display = "none";
        if(window.screen.width < 671 || window.innerWidth < 671) {
           document.getElementById("backToOnlineusers").style.setProperty("display", "flex", "important");
            document.getElementsByClassName("profile__section")[1].style.display = "none";
            document.getElementsByClassName("posts__section")[0].style.setProperty("display", "flex", "important");
            document.getElementsByClassName("textarea__section")[0].style.setProperty("display", "flex", "important");
        } 
        fetch('/api/getmessage',{method:"POST",headers: {
            'Content-Type': 'application/json'
          },
          body: JSON.stringify({receiverId: Number(e.target.parentNode.getAttribute("data-id")) })
        }).then(r =>  r.json()).then(data => setMessages(data)).then(() => window.scrollTo(0,document.body.scrollHeight))
        
        setChooseUser(Number(e.target.parentNode.getAttribute("data-id")));

    }
    function goToSettings() {
        dispatch({type: "display"})
    }
    function goToOnlineUsers() {
        dispatch({type: "none"})
    }
    const onImageChange = (event) => {
        setSendPic(event.target.files[0])
        setFileType(event.target.files[0].type)
        console.log(event.target.files[0])
         
        }
        function forceDownload(url, fileName){
            console.log(url)
            var xhr = new XMLHttpRequest();
            xhr.open("GET", url, true);
            xhr.responseType = "blob";
            xhr.onload = function(){
                var urlCreator = window.URL || window.webkitURL;
                var imageUrl = urlCreator.createObjectURL(this.response);
                var tag = document.createElement('a');
                tag.href = imageUrl;
                tag.download = fileName;
                document.body.appendChild(tag);
                tag.click();
                document.body.removeChild(tag);
            }
            xhr.send();
        }
        const backOnlineUsers = () => {
            document.getElementsByClassName("profile__section")[1].style.setProperty("display", "flex", "important");
            document.getElementsByClassName("posts__section")[0].style.setProperty("display", "none", "important");
            document.getElementsByClassName("textarea__section")[0].style.setProperty("display", "none", "important");
            document.getElementById("backToOnlineusers").style.display = "none";
        }
    return (
        <div className="main">
            <Helmet>
                <link rel="stylesheet" href="/component_css/profile.css" />
                <link rel="stylesheet" href="/component_css/profile__responsive.css" />
            </Helmet>
            <Routes>
                <Route exact path={`/edit`} element={<Editsection dispatch={dispatch}   user={user} />} />
            </Routes>
            <div id="profile" style={{display:profileDisplay}} className="profile__section" >  
                <i id="onlineUsersicon" onClick={() => { goToOnlineUsers()}} className="fas fa-arrow-circle-left"></i>         
                <img src={user.photo === null?"/avatar.png":user.photo } className="profile__picture" alt="profile" />
                <div className="firstname__section">
                    <p>{user.firstName} {user.lastName}</p>
                </div>
               
                <div className="email__section">
                    <p>{user.email}</p>
                </div>
                <button className="logout__button" onClick={logOut} >Logout</button>
                <button className="edit__button" onClick={() => { edit()}} >Edit</button>
                
            </div> 
            <div id="onlineUsers" style={{display:profileDisplay === "none"? true:"none"}}className="profile__section">
                <div onClick={() => { goToSettings()}} id="settingsIcon">
                    <i  className="fas fa-bars"></i>
                </div>
            
                <h2 >Online</h2>
                <ul>
                    {
                        activeUsers && activeUsers.map((item, index) => {
                            if(item.userId !== user.id){
                                return (
                                <li key={index} data-id={item.userId} onClick={(e) => { userChoose(e)}}><div className="active__user" data-id={item.userId}><img   width={"50px"} src={item.photo === null?"/avatar.png":item.photo } className="profile__picture" alt="profile" /><p>{item.name}</p><i style={{display: "none"}} className="fa fa-comment" aria-hidden="true"></i></div></li>
                                )
                            }
                            return null;
                        })
                    }
                </ul>
            </div>
            <div id="backToOnlineusers" onClick={() => backOnlineUsers()}><i style={{color:"black"}} className="fas fa-arrow-circle-left"></i></div>
            <div className="posts__section">

                
                <ul className="message__section">
                    {
                        Messages.map((item,index) => {
                            if(chooseUser === item.receiverId || item.userId === chooseUser) {
                                if(item.type === "image") {
                                    return (
                                        <li style={{left:item.userId === user.id?"100px":null,background: item.userId === user.id? "white": "linear-gradient(#9ed387, #77ba5a)"}}  key={index}><img onError={(e) => e.target.src = item.data } style={{height:"100px", width:"100px",transform: "translate(16%,3%)"}} alt='somemessage' src={item.data} /></li>
                                    )
                                }
                                if(item.type === "zip") {
                                    return (
                                        <li style={{left:item.userId === user.id?"100px":null,background: item.userId === user.id? "white": "linear-gradient(#9ed387, #77ba5a)"}}  key={index}><a href='' onClick={(e)=>{forceDownload(e.target.getAttribute("data-url"),`${item.data.split("/")[item.data.split("/").length-1]}.${item.type}`)}} data-url={item.data}>download:{item.data.split("/")[item.data.split("/").length-1]}.{item.type}</a></li>
                                    )
                                }
                                if(item.type === "video") {
                                    return (
                                        <li style={{left:item.userId === user.id?"100px":null,background: item.userId === user.id? "white": "linear-gradient(#9ed387, #77ba5a)", height:"300px"}}  key={index}>
                                            <div style={{height:"inherit"}}>
                                            {/* <button onClick={(e) => e.target.parentNode.children[1].style.height = "100"}>click</button> */}
                                         <video muted style={{height:"100%", width:"100%",}} controls onClick={(e) => e.target.play()} >
                                            <source src={item.data} type="video/mp4" />Your browser does not support the video tag. I suggest you upgrade your browser.
                                        </video>
                                     </div>
                                     </li>
                                    )
                                }
                                return (
                                    <li style={{left:item.userId === user.id?"100px":null}}  key={index}><p style={{background: item.userId === user.id? "white": "linear-gradient(#9ed387, #77ba5a)" }}>{item.data}</p></li>
                                )
                            }
                            return null;
                        })
                    }
                </ul>
                
            </div>   
            <div className='textarea__section'>
                    <textarea onChange={(e) => setSendMessage(e.target.value)}></textarea>
                    <button className="edit__button"  onClick={() => { send()}} ><i className="fas fa-paper-plane"></i></button>
                    <button className="edit__button" onClick={() => {document.getElementById('inpFile').click()}}><i className="fas fa-paperclip"></i></button>
                    <input id='inpFile' type="file" style={{display: "none"}} onChange={(e) => onImageChange(e) }/>
            </div>
        </div>
    )
}

export default Profile
